<template>
<div class="box"> 
  <left  @post="open"  :style="!isshow?active:null" />
  <right  v-show="isshow"/>
</div>
</template>
<script>
import left  from './left'
import right from './right'
export default {
  name: 'HelloWorld',
  components:{
    left,
    right
  },
   data () {
   return {
         isshow:true,
         active:{
           width:'100%'
         }
    }
    },
  methods:{
    open(flag){
      this.isshow = flag;
    }
  }
}
</script>

<style scoped>
 .box{
   width: 1200px;
   height: 700px;
   border: 1px solid #bbb;
   border-radius: 20px;
   margin:50px auto;
   display: flex;
 }
</style>


