<template>
    <div class="left">
     <div class="top">
     <p>拖拽/点击右侧表单项到此区域</p><p>
     <el-row>
     <el-button type="warning" size="small" @click="copy">复制代码</el-button>
     <el-button type="info" size="small" @click="open">预览</el-button>
     </el-row>
     </p> 
     </div>
     <div class="main" ref="main" :style="!flag?bad:null">
    <inforbar  v-for="(item,index) in list" :key="index" :tp="item"   style="cursor: move;"
    @handleDragStart="handleDragStart"
    @handleDragEnd="handleDragEnd"
    @handleDragOver="handleDragOver"
    @handleDragEnter="handleDragEnter"
    >
        <div :slot="item" v-if="item=='性别'" class="long">
            <div>
            <p>{{item}}：</p>
            <input type="radio" name="a" checked="true">男<input type="radio" name="a">女</div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i>
        <input type="checkbox" @change="change($event)">
        </div>
        </div>

        <div :slot="item" v-else-if="item=='上传Logo'" class="long">
        <div><p>{{item}}：</p><input type="file"></div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        </div>
        </div>

        <div :slot="item" v-else-if="item=='个人简介'" class="long">
        <div><p>{{item}}：</p><textarea name="" id="" cols="20" rows="0" :placeholder='"请输入你的"+`${item}`'></textarea></div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        <span>必填</span>
        </div>
        </div>

        <div :slot="item" v-else-if="item=='文本框'" class="long">
        <div><p>{{item.substr(0,item.length-1)}}：</p><input type="text" :placeholder='"请输入你的"+`${item.substr(0,item.length-1)}`'></div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        <span>必填</span>
        </div>
        </div>

        <div :slot="item" v-else-if="item=='下拉框'" class="long">
        <div><p>{{item}}：</p>
        <el-select v-model="value" placeholder="请选择" size="mini" style="width:200px">
        <el-option  
        v-for="val in options"
        :key="val.value"
        :label="val.label"
        :value="val.value">
        </el-option>
        </el-select>
        </div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        <span>必填</span>
        </div>
        </div>

        <div :slot="item" v-else-if="item=='单选'" class="long">
        <div>
        <p>{{item}}：</p>
        <input type="radio" name="a" checked="true"> 默认1   <input type="radio" name="a"> 默认2 
        </div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        </div>
        </div>

        <div :slot="item" v-else-if="item=='多选'" class="long">
        <div>
        <p>{{item}}：</p>
        <input type="checkbox"> 默认1   <input type="checkbox"> 默认2 
        </div>
        <div>   
        <i class="el-icon-setting"  @click="setting"></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        </div>
        </div>

        <div :slot="item" v-else-if="item=='多行文本'" class="long">
        <div>
        <p>{{item}}：</p>
        <textarea name="" id="" cols="20" rows="0" :placeholder='"请输入"+`${item.substr(2, 2)}`'></textarea>
        </div>
        <div>   
        <i class="el-icon-setting"  @click="setting"></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        </div>
        </div>

        <div :slot="item" v-else-if="item=='时间'" class="long">
        <div>
        <p>{{item}}：</p>
        <el-date-picker style="width:200px"
        v-model="value1"
        type="date"
        placeholder="选择日期"
        size="mini"
        >
        </el-date-picker>
        </div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        </div>
        </div>

        <div :slot="item" v-else-if="item=='上传'" class="long">
        <div><p>{{item}}：</p><input type="file"></div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        </div>
        </div>
        <div :slot="item" v-else class="long">
        <div><p>{{item}}：</p><input type="text"  :placeholder='"请输入"+`${item}`'></div>
        <div>   
        <i class="el-icon-setting" @click="setting" ></i>
        <i class="el-icon-delete" @click="delet(index)"></i><input type="checkbox" @change="change($event)">
        <span>必填</span>
        </div>
        </div>
    </inforbar>
      </div>      
      <delog  :title="'复制HTML代码'"  :flag='isshow' @post="show" v-show="isshow">
        <div class="scrol" slot="str">{{str}}</div>  
      </delog>
      <delog  :title="'表单信息设置'"  :flag='isshow2' @post="show2" v-show="isshow2">
        <div class="scrol" slot="ipt">
         <p style="color:red">设置功能有待完善</p> 
        </div>  
      </delog> 
     </div>
</template>

<script>
import inforbar from './bar'
import  delog    from './delog'
export default {
     data(){
     return{
        isShow:true,
        list:[],
        options: [{
          value: '请选择',
          label: '请选择'
        }, {
          value: '默认',
          label: '默认'
        }],
        value:'',
        value1: '',
        str:'',
        isshow:false,
        isshow2:false,
        dragging: null,
        bad:{
            background:'#ddd'
        },
        flag:true
       }
       },
       components:{
        inforbar,delog
       },
      methods:{
       open(){
           this.isShow = !this.isShow;
           this.$emit('post',this.isShow);
       },
       delet(idx){
           if(confirm("是否删除?")){
             this.list.splice(idx,1);
           }
       },
       change(e){
        if(e.target.checked){
            alert('选中');
        }else{
            alert('未选中');
        }
       },
       copy(){
           this.isshow = !this.isshow;
           this.str = this.$refs.main.innerHTML;
       },
       show(flag){
           this.isshow =flag;
       },
       show2(flag){
         this.isshow2 = flag;
       },
       setting(){
        this.isshow2 = !this.isshow2;
        },
        handleDragStart({e,item}){
          this.flag =  !this.flag;   
          this.dragging = item;
        },
        handleDragEnd({e,item}){
          this.dragging = null;
          this.flag =  !this.flag; 
        },
        handleDragOver({e,item}) {
         e.dataTransfer.dropEffect = 'move';
        },
        handleDragEnter({e,item}){      
        e.dataTransfer.effectAllowed = "move"
        if(item === this.dragging){
            return
        }
        const newItems = [...this.list]
        const src = newItems.indexOf(this.dragging)
        const dst = newItems.indexOf(item);
        newItems.splice(dst, 0, ...newItems.splice(src, 1))
        this.list = newItems;
        },
   },
   mounted(){
     this.watch.on('postarr',(arr)=>{
       this.list.push(arr.toString());
       let newArr =Array.from(new Set(this.list));
       this.list = newArr;
     })  
   }
}
</script>

<style scoped>
 .left{
  width: 75%;
  height: 630px;
  margin-top: 30px;
  margin-left: 30px;
  border-left: 2px dashed  #ccc; 
  border-right: 2px dashed  #ccc;
  border-bottom: 2px dashed  #ccc;  
 }
 .top{
     width: 95%;
     height: 50px;
     margin-left: 2.5%;
     display: flex;
     align-items: center;
     justify-content: space-between;
     border-top: 5px solid #bbb;
     border-bottom: 1px solid #ccc;
 }

 .main{
     height: 100%;
     margin-top: 20px;
 }
 .long{
     width: 90%;
     display: flex;
     margin: 10px  50px;
     justify-content: space-between;
 }
  .long  div{
      display: flex;
      align-items: center;
      width: 400px;
  }
  .long  div  span{
      color:blue;
  }
 .long  div input{
     margin: 0 20px;
 }
 .long  div i{
     cursor: pointer;
     margin-left: 9px;
      
 }

</style>



