<template>
    <div class="right">
     <div class="top">
     <p>自定义表单项</p>
     </div>
     <div class="main">
       <div v-for="(item,index) in list" :key="index"  :class="!item.arr[0].url?'da':'active'">
         <h4>{{item.title}}</h4>
         <div class="box">
         <div v-for="(val,ind) in item.arr" 
         :key="ind"
         ref="box" 
         :class="str==val.name &&val.url?'hezi addclass':'hezi'" 
         @click="add(val.name)"  
         draggable="true"   
         @mouseenter="dataDetails(val.name)" 
         @mouseleave="hiddenDetail"  
         @dragend="handle_end($event,val.name)"
         >
         <p :style="str==val.name &&!val.url?active:null">{{val.name}}</p>
         <img :src="val.url" alt="" v-if="val.url"> 
         </div>
         </div>
       </div>  
     </div>
    </div>
</template>

<script> 
export default {
    data(){
        return{
            list:[
                {
                    title:'常用项',
                    arr:[{name:'姓名'},{name:'电话'},{name:'邮箱'},{name:'身份证'},{name:'个人网站'},{name:'上传Logo'},{name:'性别'},{name:'职位'},{name:'个人简介'}]
                },
                  {
                    title:'自定义项',
                    arr:[{name:'文本框',url:'/static/image/add_form_img01.png'},{name:'下拉框',url:'/static/image/add_form_img05.png'},{name:'单选',url:'/static/image/add_form_img03.png'},{name:'多选',url:'/static/image/add_form_img04.png'},{name:'多行文本',url:'/static/image/add_form_img02.png'},{name:'时间',url:'/static/image/add_form_img06.png'},{name:'上传',url:'/static/image/add_form_img07.png'},{name:'省市区',url:'/static/image/add_form_img08.png'}]
                }
            ],
            str:'',
            active:{
                background:'skyblue',
                color:'#fff'
            }
        }
    },
    methods:{
        dataDetails(val,ind){
           this.str = val;
        },
        hiddenDetail(){
            this.str = '';
        },
        add(val){
         this.watch.emit('postarr',val);
        },
        handle_end(e,val) {
           if(e.offsetX<-44){
             this.watch.emit('postarr',val);
           }
        }
    }
}
</script> 

<style scoped>
 .right{
  width: 25%;
  height: 630px;
  margin-top: 30px;
  border-left: 2px dashed  #ccc; 
  border-right: 2px dashed  #ccc;
  border-bottom: 2px dashed  #ccc;  
 }
  .top{
     width: 95%;
     height: 50px;
     margin-left: 2.5%;
     border-top: 5px solid #bbb;
     border-bottom: 1px solid #ccc;
     line-height: 50px;
 }
 .main{
     padding: 0 14px;
 }
 .top p{
    margin-left: 20px;
 }
 .da .box{
     display: flex;
     flex-wrap: wrap;
 }
 .da .box p{
     padding: 6px 11px;
     background: #ccc;
     border-radius: 4px;
     margin:10px 10px;
     cursor:move;
 }
 .active .hezi{
     width: 100%;
     height: 30px;
     align-items: center;
     justify-content: space-between;
     display: flex;
     background: #ddd;
     margin-top: 10px;
     box-sizing: border-box;
     cursor: move;
 }
 .addclass{
     border: 1px solid blue;
 }
</style>



