<template>
    <div    
    draggable="true" 
    @dragstart="handleDragStart($event, tp)"
    @dragover="handleDragOver($event, tp)"
    @dragenter="handleDragEnter($event, tp)"
    @dragend="handleDragEnd($event, tp)"
    >
    <slot :name="tp"></slot>
    </div>
</template>

<script>
export default {
    props:['tp'],
    methods:{
    handleDragStart(e,item){
        this.$emit('handleDragStart',{e,item});
    },
    handleDragEnd(e,item){
       this.$emit('handleDragEnd',{e,item});
    },
    handleDragOver(e,item) {
       this.$emit('handleDragOver',{e,item});  
    },
    handleDragEnter(e,item){
       this.$emit('handleDragEnter',{e,item});
    },
    }
}
</script>

<style>

</style>



