<template>
    <div class="big-box">
     <div class="box">
      <div class="top">
      <h5>{{title}}</h5>
      <span @click="close">X</span>
      </div>
      <slot name="str"></slot>
      <slot name="ipt"></slot>
     </div>
    </div>
</template>

<script>
export default {
  props:['title','flag'],
  methods:{
      close(){
          this.$emit('post',!this.flag);
      }
  }
}
</script>

<style scoped>
.big-box{
    width: 100%;
    height:100%;
    background:rgba(100, 100, 100, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    color: #fff;
    z-index: 1;
}
.box{
    width: 50%;
    height: 50%;
    position: absolute;
    top: 10%;
    left: 25%;
    background: #fff;
    color: black;
    z-index: 100;
}
.top{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
}
span{
    position: absolute;
    top: 10px;
    right: 8px;
    cursor: pointer;
}
.scrol{
    width: 100%;
    height: 80%;
    overflow: hidden;
    overflow-y: scroll;
}
h5{
    font-size: 20px;
}
</style>



